import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import malePhoto from '../../Media/Photo/male.png';
import femalePhoto from '../../Media/Photo/female.png';
import './TeamDetails.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMars, faFlag} from '@fortawesome/free-solid-svg-icons'


const TeamDetails = () => {
    const {teamId} = useParams();
    const [team, setTeam] = useState([])
    useEffect(() => {
        const url = `https://www.thesportsdb.com/api/v1/json/1/lookupteam.php?id=${teamId}`;
        fetch(url).then((response) => response.json())
        .then(data => setTeam(data.teams[0]))
    }, [teamId]);


    const {strAlternate, strCountry, strGender, strSport, strLeague, strDescriptionDE, strTeamBanner, strStadiumDescription} = team;
    return (
        <div>
            <h1>This is team Details {teamId}</h1>
           <div className="team-box">
           <div className="team-box-description">
           <h1>{strAlternate}</h1>
            <h2> <FontAwesomeIcon icon={faFlag} /> Country: {strCountry}</h2>
            <h3><FontAwesomeIcon icon={faMars} /> {strGender}</h3>
            <h3>{strSport}</h3>
            <h3>{strLeague}</h3>
           </div>
           <div className="team-box-image">
               {
                   strGender !== 'male' ? <img src={malePhoto} alt="" /> : <img src={femalePhoto} alt="" />
               }
           </div>
           </div>

            <p>{strDescriptionDE}</p>
            <img src={strTeamBanner} alt="" />
            <p>{strStadiumDescription}</p>
        </div>
    );
};

export default TeamDetails;