import React from 'react';
import { Link } from 'react-router-dom';
import './Card.css';
const Card = (props) => {
    const {strAlternate, strTeamBadge, strSport, idTeam} = props.teamsInfo;
    return (
        <div className="card">
            <div className="img">
                <img src={strTeamBadge} alt="" />
            </div>
            <div className="card-description">
                <h2>{strAlternate}</h2>
                <p>Sports Type : {strSport}</p>
                <Link to={"/team/" + idTeam}><button>Explore</button></Link>
            </div>
        </div>
    );
};

export default Card;